<template>
  
  <div>
    <h3> Productos Codea</h3>
    <div v-for="p in productos" class="collection" :key="p.ProductoId">
      <div class="colecction-item lista-producto">
        <div>{{p.ProductoId}}</div>
        <div>{{p.Nombre}}</div>
        <div>{{p.Precio}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import db from '@/db'
export default {
  name: 'HelloWorld',
  data () {
    return {
      productos:[]
    }
  },
  created(){
  db.collection('Productos').get()
  .then(snapshot =>{
    snapshot.forEach(element => {
      this.productos.push(element.data())
    });
  })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.lista-producto{
  align-items: center;
  display: flex;
  justify-content: space-between
}
</style>



